.side_message .message_block {
    border-radius: 0;
    margin-bottom: 3px;
    opacity: 0;
    height: auto;
    padding: 6px 15px;
    -webkit-transition: .1s ease-in-out;
    transition: .1s ease-in-out;
    -webkit-transform: translateX(20px);
            transform: translateX(20px);
    width: 100%;
    z-index: -1; }

.side_message .message_block:first-child {
      border-radius: 10px 0 0 0;
      opacity: 1;
      position: relative;
      -webkit-transform: translateX(0);
              transform: translateX(0);
      z-index: 1; }

.side_message .message_block.first_active {
